import './main.css';
import raibit from '../../assets/raibit.png';
import { useNavigate } from 'react-router-dom';

function Main() {
    const navigate = useNavigate();
    const goLiveData = () => {
        navigate('/map');
    };
    const goFlowRate = () => {
        navigate('/flowrate');
    };
    
    return (
      <div id="mainRoot">
        <img src={raibit} alt="raibit" id='raibitLogo'/>
        <div id='baekwoonTitle'>BAEKWOON</div>
        <div id='baekwoonSubtitle'>   </div>
        <div id='mainButtonDiv'>
            <button id='liveDataButton' className='mainButton' onClick={() => {goLiveData()}}>  </button>
            <button id='floadButton' className='mainButton' onClick={() => {goFlowRate()}}>  </button>
            <button id='fixDataButton' className='mainButton'>  </button>
        </div>
      </div>
    );
}

export default Main;