import DongOfDaejeon from "./geojson/DongOfDajeon.json";

const fetchDongInfo = async (dong) => {
    const response = await fetch('http://127.0.0.1:5000/api/getDongInfo/' + dong);
    const yuseongInfo = await response.json();
    return yuseongInfo;
};

var yuseongInfo = await fetchDongInfo("yuseong");

async function DongFinder(kakao, guName) {
    const dongOfDaedeok = ["", "", "", "", "", "", "", "", "", "_", "", "", "", "", "", "", "", "", "", "", "", "", "_", "", "_", ""];
    const dongOfDong = ["", "", "", "", "", "_", "", "", "", "", "", "", "", "_", "", "", "", "", "", "", "", "", "", "", "_", "_", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];
    const dongOfSeo = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "_", "", "", "", "", "", "", "", "", "_", ""];
    const dongOfYuseong = ["", "", "", "", "", "", "", "", "", "", "", "", "_", "", "", "", "_", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "_", "", "", "", "", "", "", "_", "", "", "", "", "", "", "", ""];
    const dongOfJung = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];

    const colorCode = [['#39DE2A', '#A2FF99'], ['#FFC107', '#FFE400'], ['#DC3545', '#FF4848']]

    var resultDaedeok = [];
    var resultDong = [];
    var resultSeo = [];
    var resultYuseong = [];
    var resultJung = [];

    for (var i = 0; i < DongOfDaejeon.features.length; i++) {
        var dongName = DongOfDaejeon.features[i].properties.EMD_KOR_NM;
        var temp = [];
        if(dongOfDaedeok.includes(dongName)) {
            for(var j = 0; j < DongOfDaejeon.features[i].geometry.coordinates[0].length; j++) {
                temp.push(new kakao.maps.LatLng(DongOfDaejeon.features[i].geometry.coordinates[0][j][1], DongOfDaejeon.features[i].geometry.coordinates[0][j][0]));
            }
            var res = new kakao.maps.Polygon({
                path: temp,
                strokeWeight: 3,
                strokeColor: '#39DE2A',
                strokeOpacity: 0.5,
                strokeStyle: 'solid',
                fillColor: '#A2FF99',
                fillOpacity: 0.7
            })
            resultDaedeok.push(res);
        }
        else if(dongOfDong.includes(dongName)) {
            for(var j = 0; j < DongOfDaejeon.features[i].geometry.coordinates[0].length; j++) {
                temp.push(new kakao.maps.LatLng(DongOfDaejeon.features[i].geometry.coordinates[0][j][1], DongOfDaejeon.features[i].geometry.coordinates[0][j][0]));
            }
            var res = new kakao.maps.Polygon({
                path: temp,
                strokeWeight: 3,
                strokeColor: '#39DE2A',
                strokeOpacity: 0.5,
                strokeStyle: 'solid',
                fillColor: '#A2FF99',
                fillOpacity: 0.7
            })
            resultDong.push(res);
        }
        else if(dongOfSeo.includes(dongName)) {
            for(var j = 0; j < DongOfDaejeon.features[i].geometry.coordinates[0].length; j++) {
                temp.push(new kakao.maps.LatLng(DongOfDaejeon.features[i].geometry.coordinates[0][j][1], DongOfDaejeon.features[i].geometry.coordinates[0][j][0]));
            }
            var res = new kakao.maps.Polygon({
                path: temp,
                strokeWeight: 3,
                strokeColor: '#39DE2A',
                strokeOpacity: 0.5,
                strokeStyle: 'solid',
                fillColor: '#A2FF99',
                fillOpacity: 0.7
            })
            resultSeo.push(res);
        }
        else if(dongOfYuseong.includes(dongName)) {
            for(var j = 0; j < DongOfDaejeon.features[i].geometry.coordinates[0].length; j++) {
                temp.push(new kakao.maps.LatLng(DongOfDaejeon.features[i].geometry.coordinates[0][j][1], DongOfDaejeon.features[i].geometry.coordinates[0][j][0]));
            }
            var res = new kakao.maps.Polygon({
                path: temp,
                strokeWeight: 3,
                strokeColor: '#39DE2A',
                strokeOpacity: 0.5,
                strokeStyle: 'solid',
                fillColor: '#A2FF99',
                fillOpacity: 0.7
            })
            resultYuseong.push(res);
            if(yuseongInfo[dongName] === 0) {
                res.setOptions({
                    strokeColor: colorCode[0][0],
                    fillColor: colorCode[0][1]
                });
            }
            else if(yuseongInfo[dongName] === 1) {
                res.setOptions({
                    strokeColor: colorCode[1][0],
                    fillColor: colorCode[1][1]
                });
            }
            else if(yuseongInfo[dongName] === 2) {
                res.setOptions({
                    strokeColor: colorCode[2][0],
                    fillColor: colorCode[2][1]
                });
            }
        }
        else if(dongOfJung.includes(dongName)) {
            for(var j = 0; j < DongOfDaejeon.features[i].geometry.coordinates[0].length; j++) {
                temp.push(new kakao.maps.LatLng(DongOfDaejeon.features[i].geometry.coordinates[0][j][1], DongOfDaejeon.features[i].geometry.coordinates[0][j][0]));
            }
            var res = new kakao.maps.Polygon({
                path: temp,
                strokeWeight: 3,
                strokeColor: '#39DE2A',
                strokeOpacity: 0.5,
                strokeStyle: 'solid',
                fillColor: '#A2FF99',
                fillOpacity: 0.7
            })
            resultJung.push(res);
        }
        else {
            console.log("Error");
        }
    }

        if(guName === "") {
            return resultDaedeok;
        }
        else if(guName === "") {
            return resultDong;
        }
        else if(guName === "") {
            return resultSeo;
        }
        else if(guName === "") {
            return resultYuseong;
        }
        else if(guName === "") {
            return resultJung;
        }
    }

export default DongFinder;